
import { Category } from "@/interface";
import { defineComponent } from "vue";
import { useRoute } from "vue-router";

export default defineComponent({
  props: ["categories"],
  emits: ["showItemDetail", "addToOrder"],
  computed: {
    category(): Category {
      const categoryId = +useRoute().params.categoryId;
      const category = this.categories.find(
        (c: Category) => c.id === categoryId
      );
      if (category) {
        window.document.title = `${category.name} - Menu Panda`;
      }
      return category ? category : [];
    },
  },
  methods: {
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    addToOrder(id: number) {
      this.$emit("addToOrder", { id, qty: 1 });
    },
  },
});
